body {
  color: #646464;
  font-family: "Roboto", sans-serif;
  font-size: 0.75em;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table tr td,
table tr th {
  padding: .2em;
}

table thead tr {
  border-top: 1px solid #BBBBBB;
  border-bottom: 1px solid #BBBBBB;
}

table thead th {
  text-transform: uppercase;
  background: rgba(187, 187, 187, 0.2);
}

table tfoot td:nth-child(1) {
  font-weight: bold;
}

.title {
  font-weight: bold;
  font-size: 1.4em;
}

.description {
  font-size: 1em;
}

/**
* Clear inner floats
*/
.clearfix::after {
  clear: both;
  content: '';
  display: table;
}

/**
* Main content containers
* 1. Make the container full-width with a maximum width
* 2. Center it in the viewport
* 3. Leave some space on the edges, especially valuable on small screens
*/
.container {
  max-width: 1180px;
  /* 1 */
  margin-left: auto;
  /* 2 */
  margin-right: auto;
  /* 2 */
  padding-left: 20px;
  /* 3 */
  padding-right: 20px;
  /* 3 */
  width: 100%;
  /* 1 */
}

/**
* Hide text while making it readable for screen readers
* 1. Needed in WebKit-based browsers because of an implementation bug;
*    See: https://code.google.com/p/chromium/issues/detail?id=457146
*/
.hide-text {
  overflow: hidden;
  padding: 0;
  /* 1 */
  text-indent: 101%;
  white-space: nowrap;
}

/**
* Hide element while making it readable for screen readers
* Shamelessly borrowed from HTML5Boilerplate:
* https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133
*/
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.no-outline:focus {
  outline: 0;
  outline-color: transparent;
  color: none;
  background-color: transparent;
  border-color: #ced4da;
  box-shadow: 0 0 0 0;
}

.menu-user-header-p {
  padding: 10% 7.5%;
}

.font-color-ligth {
  color: #FFFFFF;
}

.list-style {
  list-style: none;
  padding: 0;
  margin: 0;
}

.upper {
  text-transform: uppercase;
}

.lower {
  text-transform: lowercase;
}

.numeric {
  text-align: right;
  padding-right: 1em;
}

.ticket {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  flex-direction: column;
  background: #fefefe;
  line-height: 1.4em;
  min-width: 300px;
  width: 390px;
  text-transform: uppercase;
}

.ticket .header {
  text-align: center;
  justify-content: center;
}

.ticket .header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ticket .header__logo img {
  max-height: 80px;
  max-width: 300px;
}

.ticket .header__description {
  text-align: center;
  padding: 5px;
  display: flex;
  flex-direction: column;
  font-size: -1.25em;
}

.ticket .header__description strong {
  padding: .2em 0em;
}

.ticket .header__description .left {
  text-align: left !important;
}

.ticket .header__number {
  margin-top: 0.5em;
}

.body {
  display: flex;
  flex-direction: column;
}

.body table {
  margin-top: 10px;
  margin-bottom: 10px;
}

.footer {
  padding: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.qr {
  text-align: center;
  margin: auto;
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  justify-content: center;
}

.qr img {
  min-width: 130px;
  max-width: 130px;
}

.divider {
  font-size: .2em;
  content: "";
  white-space: nowrap;
  color: transparent;
}

.credits {
  position: fixed;
  display: flex;
  flex-direction: column;
  top: 90%;
  left: 10%;
  background: #222222;
  padding: 10px;
  text-align: center;
  color: #ffffff;
  line-height: 1.5em;
}

.credits a {
  font-weight: bold;
  color: #fefefe;
  text-decoration: none;
}

.credits a:hover, .credits a:focus {
  color: #cccccc;
}

@media print {
  body {
    display: block;
  }
  .ticket {
    width: 100%;
    padding: 0px;
  }
  .credits {
    display: none;
  }
}

.invoice {
  text-transform: uppercase;
  width: 960px !important;
  padding: .2em;
  line-height: 1.4em;
  font-size: 1.1em;
}

.invoice .header {
  width: 100%;
  max-height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.invoice .header__logo {
  width: 200px;
  justify-content: center;
  align-items: center;
}

.invoice .header__logo img {
  width: 100%;
}

.invoice .header__description {
  width: calc( 100% - 400px);
  margin: 0 .4em 0 .4em;
  padding: .5em;
}

.invoice .header__number {
  width: 200px;
  padding: .6em;
  border: 1px solid #BBBBBB;
  border-radius: 1.3em;
  font-size: 1.3em;
  text-align: center;
  line-height: 1.8em;
}

.invoice .header__number .title:nth-child(2) {
  color: #FC4136;
}

.invoice .body {
  margin-top: .5em;
  display: flex;
  flex-direction: column;
}

.invoice .body table {
  margin-top: 5px;
  margin-bottom: 5px;
}

.invoice .info tr td:nth-child(1), .invoice .info tr td:nth-child(3), .invoice .info tr td:nth-child(5), .invoice .info tr td:nth-child(7), .invoice .info tr td:nth-child(9) {
  width: 150px;
}

.invoice .details tr td {
  width: 75%;
}

.invoice .qr {
  flex-direction: row !important;
  text-align: left !important;
}

.invoice .qr div {
  padding: 0 .6em 0 0;
}

.invoice .qr div small {
  display: block;
}
