// -----------------------------------------------------------------------------
// This file contains CSS helper classes.
// -----------------------------------------------------------------------------

/**
* Clear inner floats
*/

.clearfix::after {
    clear: both;
    content: '';
    display: table;
}


/**
* Main content containers
* 1. Make the container full-width with a maximum width
* 2. Center it in the viewport
* 3. Leave some space on the edges, especially valuable on small screens
*/

.container {
    max-width: $max-width;
    /* 1 */
    margin-left: auto;
    /* 2 */
    margin-right: auto;
    /* 2 */
    padding-left: 20px;
    /* 3 */
    padding-right: 20px;
    /* 3 */
    width: 100%;
    /* 1 */
}


/**
* Hide text while making it readable for screen readers
* 1. Needed in WebKit-based browsers because of an implementation bug;
*    See: https://code.google.com/p/chromium/issues/detail?id=457146
*/

.hide-text {
    overflow: hidden;
    padding: 0;
    /* 1 */
    text-indent: 101%;
    white-space: nowrap;
}


/**
* Hide element while making it readable for screen readers
* Shamelessly borrowed from HTML5Boilerplate:
* https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133
*/

.visually-hidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.no-outline {
    &:focus {
        outline: 0;
        outline-color: transparent;
        color: none;
        background-color: transparent;
        border-color: #ced4da;
        box-shadow: 0 0 0 0;
    }
}

//padin para el menu del user en el header
.menu-user-header-p {
    padding: 10% 7.5%;
}

//color de texto blaco
.font-color-ligth {
    color: $color-light;
}

// son lista de estilos para los ul
.list-style {
    list-style: none;
    padding: 0;
    margin: 0;
}

.upper {
    text-transform: uppercase;
}

.lower {
    text-transform: lowercase;
}

.numeric {
    text-align: right;
    padding-right: 1em;
}